import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Event } from '../events/event';
import { UsersService } from "../users/users.service";
import { EventsService } from "../events/events.service";
import { RegistrationService } from "./registration.service";
import { Registration } from '../models/registration';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-regs',
  templateUrl: './view-registration.component.html',
  styleUrls: ['./view-registration.component.css']
})

export class ViewRegistrationComponent implements OnInit {
  regs: any[];
  event: Event;
  title = 'View Registrations';

  constructor(private toastr: ToastrService, private _regService: RegistrationService, private _userService: UsersService, private _eventService: EventsService, private router: Router, private route: ActivatedRoute) {
  }
  ngOnInit(): void {
    if (this._userService.isLoggedIn()) {
      if (this._userService.getRole() === 'admin') {
        this._eventService.getEvent(this.route.snapshot.params.id).subscribe((event: Event) => {
          this.event = event;
        });
        this._regService.getReg(this.route.snapshot.params.id).subscribe(
          (registrations: any) => {
            this.regs = registrations;
            console.log(this.regs);
          }
        );

      }
    } else {
      this.router.navigate(['login']);
    }
  }

  onSubmit(formValue: any) {
    var winner;
    for (var key in formValue) {
      if (formValue[key] === true) {
        winner = key;
        break;
      }
    }

    // formValue.members.push(localStorage.getItem("email"));


    this.event.winner = winner;
    this._eventService.updateEvent(this.event).subscribe((event: Event) => {
      this.toastr.success("Winner Declared and Published!!! :)");
    });

    this._regService.updateReg(winner).subscribe(val => {
      this.toastr.success("Winner Choosen!!! :)");
    });
  }
}
