import { Component, OnInit } from '@angular/core';
import { Event } from '../events/event';
import { Registration } from '../models/registration'
import { EventsService } from '../events/events.service';
import { UsersService } from '../users/users.service';
import { RegistrationService } from '../registration/registration.service';


@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {
  events: Event[];
  events_open: Event[];
  events_closed: Event[];
  userReg: Registration[];
  loggedIn: any;

  constructor(private _eventsService: EventsService, private _userService: UsersService, private _regService: RegistrationService) {

  }


  ngOnInit() {
    this.loggedIn = this._userService.isLoggedIn();
    this._eventsService.getEvents().subscribe(
      (events: any) => {
        this.events = events;
        this.events_open = events.filter(event => event.status.toString().toLowerCase() === 'open');
        this.events_closed = events.filter(event => event.status.toString().toLowerCase() !== 'open');
        this.events_closed.forEach(element => {
          this._regService.getRegistrationsById(element.winner).subscribe((reg: Registration) => {
            element.winnerName = reg.teamName;
          });
        });

      },
      err => console.log(err)
    );


    this._userService.getRegistrations().subscribe((registration: any) => {
      this.userReg = registration.map(reg => {
        return parseInt(reg.eventId);
      });

    });
  }
}
